.cmtlist li { margin-bottom: 1.5em; padding-bottom: 7px; border-top: 1px dashed #B8B8B8; }

.cmtlist li:first-child { border-top: none; }

.cmtlist li button { cursor: pointer; padding: 3px 6px; border: 1px solid #CACACA; background: #FAFAFA; float: left; margin: 0 1px; font-size: 13px; line-height: 1.1; }

.cmtlist li button:hover, .cmtlist li button:active { background-color: #eee; }

.cmtlist li .cmt_content .board_comment { padding: 0 10px; color: #707070; line-height: 1.5; }

.cmtlist li .cmt_content .cmt_h { overflow: hidden; margin: 1.5em 10px 0.5em; }

.cmtlist li .cmt_content .cmt_h .editText .cmtEditarea, .cmtlist li .cmt_content .cmt_h .cmt_btn .deleteBtn, .cmtlist li .cmt_content .cmt_h .writer_nm { float: left; }

.cmtlist li .cmt_content .cmt_h .cmt_btn { float: right; }

.cmtlist li .cmt_content .cmt_h .cmt_btn .edit_btn { float: left; margin-right: 5px; }

.cmtlist li .cmt_content .cmt_h .cmt_btn .edit_cancelBtn { float: left; }

.cmtlist li .cmt_content .cmt_h .cmt_btn span.pipe_line { float: left; margin-right: 5px; }

.cmtlist li .cmt_content .cmt_h .writer_nm .cmt_writer { font-size: 14px; font-weight: bold; }

.cmtlist li .cmt_content .cmt_h .writer_nm .cmt_date { font-size: 12px; }

.cmtlist li .cmt_content .editText { padding: 0 10px; position: relative; }

.cmtlist li .cmt_content .editText .byte-info { font-size: 13px; padding-top: 5px; line-height: 1.1; }

.cmtlist li .cmt_content .editText .cmt_text textarea { width: 100%; padding: 5px 65px 5px 5px; height: 80px; }

.cmtlist li .cmt_content .editText .cmt_btn { position: absolute; top: 21px; right: 27px; }

.cmtlist li .cmt_content .editText .cmt_btn button { width: 40px; height: 40px; text-align: center; }

@media screen and (max-width: 768px) { .board-wrap.gallery-list .bbs-list > li { width: 50%; }
  .board-wrap.normal-list.busi_offer .bbs-list .layout-table { display: block; }
  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li { display: block; }
  .board-wrap.normal-list.busi_offer .bbs-list > li { width: 100%; }
  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_class span { height: 28px; line-height: 26px; border-radius: unset; }
  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_class { display: inline-block; }
  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.num { display: none; }
  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_img { width: 100%; }
  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_img .img_wrap { position: relative; padding-top: 35%; width: 50%; }
  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_img .img_wrap img { position: absolute; top: 0; left: 0; }
  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.content .top_cont .subject, .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.content .top_cont .sector { float: none; }
  .board-wrap.normal-list.busi_offer + .btns-area .btn-group { padding: 0; position: relative; display: block; right: auto; top: auto; text-align: right; }
  .board-wrap.normal-list.busi_offer + .btns-area .btn-group .btn-custom { float: none; } }

@media screen and (max-width: 600px) { .board-wrap.normal-list.thumbnail .bbs-list .board-info { margin-top: 10px; position: static; }
  .board-wrap.normal-list.thumbnail .bbs-list .item .layout-table { display: block; }
  .board-wrap.normal-list.thumbnail .bbs-list .item .layout-table > li { display: block; }
  .board-wrap.normal-list.thumbnail .bbs-list .item .layout-table > li.thumbnail-img { width: 100%; max-width: 300px; margin: 0 auto; }
  .board-wrap.normal-list.thumbnail .bbs-list .item .layout-table > li.thumbnail-img a { height: 200px; }
  .board-wrap.normal-list.thumbnail .bbs-list .item .layout-table > li.content { padding: 0 0 15px; } }

@media screen and (max-width: 480px) { .is-hidden-480 { display: none !important; }
  .board-wrap .board-header .level-right .is-grouped { display: block; }
  .board-wrap .board-header .level-right .is-grouped .control { width: 100%; margin-bottom: 10px; }
  .board-wrap .board-header .level-right .is-grouped .control:last-child { margin-bottom: 0; }
  .board-wrap .board-header .level-right .is-grouped .control .select, .board-wrap .board-header .level-right .is-grouped .control .select select, .board-wrap .board-header .level-right .is-grouped .control .input, .board-wrap .board-header .level-right .is-grouped .control .button { width: 100%; }
  .board-wrap.gallery-list .bbs-list > li { width: 100%; max-width: 375px; margin: 0 auto; float: none; } }

@media screen and (max-width: 425px) { .board-wrap.gallery-list .bbs-list > li { width: 100%; }
  .section-comment-list .comment-list .comment-info .info-date { position: relative; display: block; } }
