.cmtlist {
  margin-top: 2em;
  border-top:2px solid #B3B3B3;
  li {
    margin-bottom:1.5em;
    padding-bottom:7px;
    border-top:1px dashed #B8B8B8;
    &:first-child{
      border-top:none;
    }
    button {
      cursor: pointer;
      padding:3px 6px;
      border:1px solid #CACACA;
      background:#FAFAFA;
      float: left;
      margin:0 1px;
      font-size: 13px;
      line-height: 1.1;
      &:hover, &:active {
        background-color: #eee;
      }
    }
    .cmt_content {
      .board_comment{padding:0 10px;color:#707070; line-height: 1.5;}
      .cmt_h{
        overflow:hidden;
        margin:1.5em 10px 0.5em;
        .editText .cmtEditarea, .cmt_btn .deleteBtn, .writer_nm {
          float:left;
        }
        .cmt_btn {
          float:right;
          .edit_btn {
            float:left;margin-right:5px;
          }
          .edit_cancelBtn{float:left}
          span.pipe_line{float:left;margin-right:5px}
        }
        .writer_nm {
          .cmt_writer {
            font-size:14px;
            font-weight:bold;
          }
          .cmt_date {
            font-size:12px;
          }
        }        
      }
      .editText {
        padding:0 10px;
        position: relative;
        .byte-info {
          font-size: 13px;
          padding-top: 5px;
          line-height: 1.1;
        }
        .cmt_text {
          textarea {
            width:100%;
            padding:5px 65px 5px 5px;
            height:80px;
          }
        }
        .cmt_btn {
          position: absolute;
          top:21px;
          right:27px;          
          button {
            width: 40px;
            height: 40px;
            text-align: center;
          }

        }
      }
    }
  } 
}
@media screen and (max-width: 768px) {
    .board-wrap {
        &.gallery-list {
            .bbs-list {
                > li {
                    width: 50%;
                }
            }
        }
    }




  .board-wrap.normal-list.busi_offer .bbs-list .layout-table {
    display: block;
  }

  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li {
    display: block;
  }

  .board-wrap.normal-list.busi_offer .bbs-list > li {
    width: 100%;
  }

  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_class span {
    height: 28px;
    line-height: 26px;
    border-radius: unset;
  }

  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_class {
    display: inline-block;
  }

  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.num {
    display: none;
  }

  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_img {
    width: 100%;
  }

  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_img .img_wrap {
    position: relative;
    padding-top: 35%;
    width: 50%
  }

  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.offer_img .img_wrap img {
    position: absolute;
    top: 0;
    left: 0;
  }

  .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.content .top_cont .subject, .board-wrap.normal-list.busi_offer .bbs-list .layout-table > li.content .top_cont .sector {
    float: none;
  }

  .board-wrap.normal-list.busi_offer + .btns-area  {
    .btn-group {
      padding: 0;
      position: relative;
      display: block;
      right: auto;
      top: auto;
      text-align: right;
      .btn-custom {
        float: none;
      }
    }
  }
}
@media screen and (max-width: 600px) {
    .board-wrap.normal-list {
        &.thumbnail {
            .bbs-list {
                .board-info {
                    margin-top: 10px;
                    position: static;
                }
                .item {
                    .layout-table {
                        display: block;
                        > li {
                            display: block;
                            &.thumbnail-img {
                                width: 100%;
                                max-width: 300px;
                                margin: 0 auto;
                                a {
                                    height: 200px;
                                }
                            }
                            &.content {
                                padding: 0 0 15px;
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 480px) {
    .is-hidden-480 {
        display: none !important;
    }
    .board-wrap {
        .board-header {
            .level-right {
                .is-grouped {
                    display: block;
                    .control {
                        width:100%;
                        margin-bottom: 10px;
                        &:last-child {
                            margin-bottom: 0;
                        }
                        .select, .select select, .input, .button {
                            width: 100%;
                        }
                    }
                }
            }
        }
        &.gallery-list {
            .bbs-list {
                > li {
                    width: 100%;
                    max-width: 375px;
                    margin: 0 auto;
                    float: none;
                }
            }
        }
    }
}

@media screen and (max-width: 425px) {
  .board-wrap.gallery-list .bbs-list > li {
    width: 100%;
  }

  .section-comment-list .comment-list .comment-info .info-date {
    position: relative;
    display: block;
  }
}

